.cart__column {
  border-top-right-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
  background: white;
}
.cart__column__header {
  color: white;
  background-color: #1d1d1d;
  border-top-right-radius: 0.8rem;
  height: 3.4rem;
  white-space: nowrap;
}
.cart__column__header__icon:before,
.cart__column__header__icon:after {
  font-size: 2.3rem;
  margin: 0 0 0 1rem;
  vertical-align: middle;
}
.cart__column__header h4 {
  display: inline-block;
  vertical-align: middle;
  font-size: 1.1rem;
  margin: 0.2rem 0 0 1.2rem;
  font-weight: 200;
}
.cart__column__cart {
  min-height: 11rem;
  text-align: center;
  background-size: cover;
  color: white;
}
.shop__overview__column {
  margin-top: 1.4rem;
}
