.map__view {
  background-color: #e8e0d5;
  border-radius: 0.6rem;
  border: 1px solid #5a5a5a;
  min-height: 29rem;
  height: calc(100vh - 25rem);
  margin: 1.6rem 0 0 0;
  text-align: center;
  position: relative;
}
.map__view__header {
  background-color: #5a5a5a;
  border-radius: 0.5rem 0.5rem 0 0;
  height: 3.75rem;
}
