.pickup__map {
  font-size: 1rem;
}
.pickup__map__info {
  margin: 1rem 0.5rem;
}
.pickup__map button {
  width: 90%;
  position: absolute;
  bottom: 0;
  left: 5%;
}
.gm-style .gm-style-iw {
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  width: auto !important;
  min-width: 8rem;
  min-height: 5rem !important;
  height: 8rem !important;
  padding: 0;
}
.gm-style .gm-style-iw > div:first-of-type {
  margin: 0;
  width: 100%;
  max-width: initial !important;
}
